.select {
    width: calc(50% - 9px);

    position: relative;

    @media(max-width:767px) {
        width: calc(50% - 5px);
        margin-bottom: 10px;
    }

    @media(max-width:549px) {
        width: 100%;
    }

    &.active {
        z-index: 120;

        .select__content {
            z-index: 120;
        }
    }
}

.select__top {
    display: flex;
    align-items: center;
    height: 60px;
    padding: 0 50px 0 30px;

    cursor: pointer;

    box-shadow: 0 18px 24px rgba(118, 118, 118, 0.14);
    border-radius: 30px;
    border: 1px solid #c4c4c4;
    background-color: #ffffff;

    position: relative;

    @media(max-width:768px) {
        height: 50px;
    }

    &::before {
        content: '';
        width: 14px;
        height: 10px;

        background-image: url('../img/select-arrow.svg');

        position: absolute;
        right: 25px;
        top: calc(50% - 5px);
        // transform: translateY(-50%);
        z-index: 2;

        transition: .3s linear;
    }

    &.active::before {
        transform: rotate(180deg);
    }
}

.select__top-title {
    pointer-events: none;
    color: #797979;
    font-family: Gilroy;
    @include adaptive("font-size", 15, 13);
    letter-spacing: 0.38px;
}

.select__content {
    position: absolute;
    top: calc(100% + 1px);
    left: 0;
    right: 0;
    z-index: 22;

    display: none;
    max-height: 165px;
    overflow-y: scroll;
    padding: 10px 0;

    box-shadow: 0 18px 24px rgba(118, 118, 118, 0.14);
    border-radius: 30px;
    border: 1px solid #c4c4c4;
    background-color: #ffffff;

    @media(max-width:768px) {
        max-height: 100px;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}


.select__input {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;

    position: relative;
    transition: all .3s;

    &:hover {
        .select__item {
            font-weight: 700;
            color: black;
        }
    }

    cursor: pointer;

    &:last-child {
        border-bottom: none;
    }

    input {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;

        &:checked+.select__item {
            font-weight: 700;
            color: black;
        }
    }
}

.select__item {
    width: 100%;

    color: #797979;
    font-family: Gilroy;
    @include adaptive("font-size", 15, 13);
    font-weight: 400;
    letter-spacing: 0.38px;
    font-weight: 300;

    padding: 13px 50px 13px 30px;
    display: block;
}

.select__img-inner {
    margin: 0 10px;
}